import { Component, OnInit } from '@angular/core';

import { SoundtrackService } from './services';
import { SoundtrackModel } from './models';

@Component({
  selector: 'dod-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit {
  public title: string = 'DOODI';
  public soundConfig: SoundtrackModel = {
    songUrl: 'assets/soundtracks/main_soundtrack.mp3',
    loop: true,
    volume: 0.3
  };

  constructor(private soundtrackService: SoundtrackService) { }

  ngOnInit(): void {
    this.soundtrackService.playSoundtrack(this.soundConfig);
  }

}
