import {
  Component,
  Input,
  Output,
  EventEmitter,
} from '@angular/core';

export enum ButtonType {
  Primary = 'primary',
  Choice = 'choice',
  Point = 'point',
  Location = 'location',
  Health = 'health',
  Inventory = 'inventory',
  ItemInventory = 'item-inventory',
  GameOver = 'game-over',
  Arrow = 'arrow',
  Perk = 'perk',
}

@Component({
  selector: 'dod-button',
  templateUrl: './button.component.html',
  styleUrls: ['./button.component.scss']
})
export class ButtonComponent {
  @Input() public caption: string = '';
  @Input() public type: ButtonType = ButtonType.Primary;
  @Input() public positionTop: string;
  @Input() public positionLeft: string;
  @Input() public id: string;
  @Input() public isDisabled: boolean;
  @Input() public isActive: boolean;
  @Input() public name: string;
  @Input() public tooltipMessage: string = '';
  @Output() public buttonClick: EventEmitter<string> = new EventEmitter();

  public onButtonClick(type: string): void {
    this.buttonClick.emit(type);
  }

}
