import { Component } from '@angular/core';

import { SoundtrackService } from '../../services';

@Component({
  selector: 'dod-settings-page',
  templateUrl: './settings-page.component.html',
  styleUrls: ['./settings-page.component.scss']
})
export class SettingsPageComponent {
  public isSongPlay: string = '';
  constructor(private soundtrackService: SoundtrackService) { }

  public toggleSong(): void {
    if (this.isSongPlay === '') {
      this.soundtrackService.muteSoundtrack();
      this.isSongPlay = '';
    } else {
      this.soundtrackService.playSoundtrack();
      this.isSongPlay = '';
    }
  }

}
