import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { environment } from '../../environments/environment';

@Injectable({
  providedIn: 'root'
})
export class EventService {
  public baseApi: string = environment.baseApi;
  public characterId: string = environment.character_id;

  constructor(private http: HttpClient) { }

  public sendPersonChoice(path: string): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/${path}`, {
      character_id: window.localStorage.getItem('character_id') }, {
        headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

  public changeHP(useValue: number): Observable<any> {
    return this.http.post(`${this.baseApi}/character_health_change/`,
      { character_id: window.localStorage.getItem('character_id'), value: useValue }, {
        headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

  public changeStamina(useValue: number): Observable<any> {
    return this.http.post(`${this.baseApi}/character_stamina_change/`,
      { character_id: window.localStorage.getItem('character_id'), value: useValue }, {
        headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

  public foundItem(useValue: string): Observable<any> {
    return this.http.post(`${this.baseApi}/character_get_item/`,
      { character_id: window.localStorage.getItem('character_id'), item: useValue }, {
        headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

}
