import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { BehaviorSubject } from 'rxjs';

import { ModalComponent } from 'src/app/components';
import { PersonService } from 'src/app/services';

@Component({
  selector: 'dod-menu-page',
  templateUrl: './menu-page.component.html',
  styleUrls: ['./menu-page.component.scss']
})
export class MenuPageComponent implements OnInit {
  public isLogged = new BehaviorSubject<boolean>(false);
  public isLoaderShown: boolean = false;
  public isCharacterCreate = new BehaviorSubject<boolean>(false);

  constructor(
    private router: Router,
    private dialog: MatDialog,
    private personService: PersonService,
  ) { }

  ngOnInit(): void {
    this.checkIsLogged();
    this.checkCharacterId();
  }

  public checkIsLogged(): void {
    this.isLogged.next(Boolean(window.localStorage.getItem('token')));
  }

  public checkCharacterId(): void {
    this.isCharacterCreate.next(Boolean(window.localStorage.getItem('character_id')));
  }

  public playGame(): void {
    this.router.navigate(['/playground-map']);
  }

  public signIn(): void {
    this.router.navigate(['/login']);
  }

  public signUp(): void {
    this.router.navigate(['/registration']);
  }

  public perk(): void {
    this.router.navigate(['/perks']);
  }

  public createNewCharacter(): void {
    this.isLoaderShown = true;
    this.personService.createPerson().subscribe(data => {
      window.localStorage.setItem('character_id', data.id);
      this.checkCharacterId();
      this.isLoaderShown = false;
    });
  }

  public exitGame(): void {
    window.localStorage.removeItem('token');
    window.localStorage.removeItem('character_id');
    this.dialog.open(ModalComponent, {
      height: '300px',
      width: '300px',
      data: {
        header: 'D.O.O.D.I',
        text: '   '
      }
    });

    this.checkIsLogged();
  }

}
