import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { environment } from '../../environments/environment';
import { RegistrationModel, LoginModel } from '../models';

@Injectable({
  providedIn: 'root'
})
export class AuthService {
  public baseApi: string = environment.baseApi;

  constructor(private http: HttpClient) { }

  public userRegistration(data: RegistrationModel): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/r/sign_up/`, data);
  }

  public userLogin(data: LoginModel): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/r/sign_in/`, data);
  }

}
