import { Component } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';

import { AuthService } from 'src/app/services';
import { ModalComponent } from 'src/app/components';

@Component({
  selector: 'dod-login-page',
  templateUrl: './login-page.component.html',
  styleUrls: ['./login-page.component.scss']
})
export class LoginPageComponent {
  public profileLogin: FormGroup = new FormGroup({
    email: new FormControl('', [Validators.required, Validators.email]),
    password: new FormControl('', [Validators.required]),
  });
  public isLoaderShown: boolean = false;

  constructor(
    private router: Router,
    private authService: AuthService,
    private dialog: MatDialog
  ) { }

  public login(): void {
    this.isLoaderShown = true;
    this.authService.userLogin(this.profileLogin.value).subscribe({
      error: (_err) => {
        this.dialog.open(ModalComponent, {
          height: '200px',
          width: '200px',
          data: {
            header: '',
            text: '   ',
          }
        });
        this.isLoaderShown = false;
      },
      next: (data: { token: string }) => {
        this.isLoaderShown = false;
        window.localStorage.setItem('token', data.token);
        this.router.navigate(['/']);
      }
    });
  }

}
