import { Component, Inject, OnInit, EventEmitter } from '@angular/core';
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { EventActions } from 'src/app/models';

@Component({
  selector: 'dod-modal',
  templateUrl: 'modal.component.html',
  styleUrls: ['modal.component.scss'],
})
export class ModalComponent implements OnInit {
  public classSelector: string;
  public eventActions: EventActions;
  public gameOver: EventActions;
  public locationEvents: EventActions;
  public inventory: Array<string>;
  public transferDescription: string;
  public doChoiceAction: EventEmitter<string> = new EventEmitter<string>();
  public useItemFromInventoryAction: EventEmitter<string> = new EventEmitter<string>();
  public redirectToMainAction: EventEmitter<any> = new EventEmitter<any>();
  public locationEventHandlerAction: EventEmitter<string> = new EventEmitter<string>();
  constructor(@Inject(MAT_DIALOG_DATA) public data) { }

  ngOnInit(): void {
    this.renderEventActions();
    this.renderInventory();
    this.renderGameOver();
    this.renderLocationEvents();
    this.createModalSelector();
    this.createTransferText();
  }

  public renderEventActions(): void {
    this.eventActions = this.data.actions;
  }

  public renderGameOver(): void {
    this.gameOver = this.data.gameOver;
  }

  public renderLocationEvents(): void {
    this.locationEvents = this.data.locationEvents;
  }

  public renderInventory(): void {
    if (this.data.inventory) {
      this.inventory = this.data.inventory._value.character_inventory;
    }
  }

  public createModalSelector(): void {
    this.classSelector = this.data.header.replace(/ /g, '-').toLowerCase();
  }

  public doChoice(action: string): void {
    this.doChoiceAction.emit(action);
  }

  public useItemFromInventory(item: string): void {
    this.useItemFromInventoryAction.emit(item);
  }

  public redirectToMain(): void {
    this.redirectToMainAction.emit();
  }

  public locationEventHandler(action: string): void {
    this.locationEventHandlerAction.emit(action);
  }

  public createTransferText(): void {
    if (this.data.text) {
      this.transferDescription = this.data.text;
    }
  }

}
