import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { HttpClientModule } from '@angular/common/http';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import {
  ButtonComponent,
  PlaygroundComponent,
  PlaygroundLoaderComponent,
  PersonComponent,
  PersonIndicatorComponent,
  ModalComponent,
} from './components';
import {
  DevelopersPageComponent,
  MenuPageComponent,
  SettingsPageComponent,
  RegistrationPageComponent,
  LoginPageComponent,
  PerksPageComponent
} from './pages';
import { MaterialModule } from './material.module';
import { ServiceWorkerModule } from '@angular/service-worker';
import { environment } from '../environments/environment';

const routes: Routes = [
  { path: '', component: MenuPageComponent },
  { path: 'playground-map', component: PlaygroundComponent },
  { path: 'about-us', component: DevelopersPageComponent },
  { path: 'settings', component: SettingsPageComponent },
  { path: 'registration', component: RegistrationPageComponent },
  { path: 'login', component: LoginPageComponent },
  { path: 'perks', component: PerksPageComponent },
];

@NgModule({
  declarations: [
    AppComponent,
    ButtonComponent,
    PlaygroundComponent,
    MenuPageComponent,
    DevelopersPageComponent,
    PlaygroundLoaderComponent,
    PersonComponent,
    PersonIndicatorComponent,
    ModalComponent,
    SettingsPageComponent,
    RegistrationPageComponent,
    LoginPageComponent,
    PerksPageComponent,
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    BrowserAnimationsModule,
    MaterialModule,
    ReactiveFormsModule,
    RouterModule.forRoot(routes),
    ServiceWorkerModule.register('ngsw-worker.js', { enabled: environment.production }),
  ],
  providers: [],
  bootstrap: [AppComponent],
  entryComponents: [ModalComponent],
})
export class AppModule { }
