import { Component, OnInit } from '@angular/core';

import { perks } from '../../options';
import { Perk } from '../../models';
import { PersonService } from 'src/app/services';

@Component({
  selector: 'dod-perks-page',
  templateUrl: './perks-page.component.html',
  styleUrls: ['./perks-page.component.scss']
})
export class PerksPageComponent implements OnInit {
  public perksOption: Perk[] = perks;
  public currentItemLinks: number[];
  public perks: any = [];
  public availablePoints: number = 0;
  public isLoaderShown: boolean = false;

  constructor(private personService: PersonService) { }

  ngOnInit(): void {
    this.getPersonPerks();
  }

  public getPersonPerks(): void {
    this.personService.getPersonInfo().subscribe(person => {
      this.perks = person.character_perks;
      this.availablePoints = person.character_exp;
    });
  }

  public activePerks(perkName: string) {
    this.isLoaderShown = true;
    switch (perkName) {
      case 'armor':
        this.personService.armorPerkUp().subscribe(data => {
          this.isLoaderShown = false;
          this.getPersonPerks();
        });
        break;
      case 'pioneer':
        this.personService.luckyPerkUp().subscribe(data => {
          this.isLoaderShown = false;
          this.getPersonPerks();
        });
        break;
      case 'stamina':
        this.personService.staminaPerkUp().subscribe(data => {
          this.isLoaderShown = false;
          this.getPersonPerks();
        });
        break;
      case 'survivor':
        this.personService.healthPerkUp().subscribe(data => {
          this.isLoaderShown = false;
          this.getPersonPerks();
        });
        break;
    }
  }

}
