import { Injectable } from '@angular/core';
import { Howl, Howler } from 'howler';

import { SoundtrackModel } from '../models';

@Injectable({
  providedIn: 'root'
})
export class SoundtrackService {
  public sound: Howl;
  public soundrtackDefaultConfig: SoundtrackModel = {
    songUrl: 'assets/soundtracks/main_soundtrack.mp3',
    loop: true,
    volume: 0.3
  };

  public playSoundtrack(config: SoundtrackModel = this.soundrtackDefaultConfig): void {
    this.sound = new Howl({
      src: [config.songUrl],
      loop: config.loop,
    });
    this.sound.play();

    Howler.volume(config.volume);
  }

  public muteSoundtrack(): void {
    this.sound.stop();
  }
}
