import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';

import { PersonService } from 'src/app/services';
import { Person } from 'src/app/models';
import { ModalComponent } from '../shared';

@Component({
  selector: 'dod-person',
  templateUrl: './person.component.html',
  styleUrls: ['./person.component.scss']
})
export class PersonComponent implements OnInit {
  public person = this.personService.getPersonState();
  public isLoaderShown: boolean = false;
  constructor(
    private personService: PersonService,
    private dialog: MatDialog
  ) { }

  public ngOnInit(): void {
    this.getPersonStats();
  }

  public getPersonStats(): void {
    this.personService.getPersonInfo().subscribe((person: Person) => {
      this.personService.updatePersonStats(person);
    });
  }

  public openInventory(): void {
    const openDialog = this.dialog.open(ModalComponent, {
      height: '700px',
      width: '700px',
      data: {
        header: 'Inventory',
        inventory: this.person,
      },
    });

    openDialog.componentInstance.useItemFromInventoryAction.subscribe((item: number) => {
      this.isLoaderShown = true;
      this.personService.useItemFromInventory(item).subscribe(() => {
        this.getPersonStats();
        this.dialog.closeAll();
        this.isLoaderShown = false;
      });
    });
  }

}
