import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';

import { Steps, Locations, BackendLocations, Person, TransitionArrows } from '../../models';
import { steps, locations, transitionArrowsOption } from '../../options';
import { LocationService, EventService, PersonService } from 'src/app/services';
import { ModalComponent } from '../shared';

@Component({
  selector: 'dod-playground',
  templateUrl: './playground.component.html',
  styleUrls: ['./playground.component.scss'],
})

export class PlaygroundComponent implements OnInit {
  public steps: Steps[] = steps;
  public renderLocations: Locations[] = [];
  public currentItemLinks: string[];
  public isLoaderShown: boolean = false;
  public transitionalArrows: TransitionArrows[] = transitionArrowsOption;

  constructor(
    private locationService: LocationService,
    private dialog: MatDialog,
    private eventService: EventService,
    private personService: PersonService,
    private router: Router,
  ) { }

  public ngOnInit(): void {
    this.getLocations();
    this.handleUserDeath();
  }

  public getLocations(): void {
    this.isLoaderShown = true;
    this.locationService.getLocations()
      .subscribe((data: BackendLocations[]) => {
        this.transformLocations(data);
        this.isLoaderShown = false;
      });
  }

  public handleUserDeath(): void {
    this.personService.getPersonInfo().subscribe((person: Person) => {
      if (person.character_health <= 0 || person.character_stamina <= 0) {
        const openedDialog = this.dialog.open(ModalComponent, {
          height: '300px',
          width: '300px',
          data: {
            header: 'You are died',
            gameOver: [
              {
                action: 'redirectToMain',
                description: 'Play Again'
              }
            ]
          },
          disableClose: true,
        });

        openedDialog.componentInstance.redirectToMainAction.subscribe(_ => {
          this.router.navigate(['/']);
          openedDialog.close();
        });
      }
    });
  }

  public getValueFromEvent(event: any, actionName: string): number {
    let value = 0;
    event.forEach(item => {
      if (item.action === actionName) {
        value = item.value;
      }
    });
    return value;
  }

  public createEventModal(item): void {
    if (item.event_name) {
      const openedDialog = this.dialog.open(ModalComponent, {
        height: '700px',
        width: '700px',
        data: {
          header: item.event_name,
          actions: item.event
        },
        disableClose: true
      });

      openedDialog.componentInstance.doChoiceAction.subscribe((data: string) => {
        switch (data) {
          case '-Stamina': {
            const value = this.getValueFromEvent(item.event, '-Stamina');
            this.eventService.changeStamina(-value).subscribe(() => {
              this.personService.getPersonInfo().subscribe((person: Person) => {
                this.personService.updatePersonStats(person);
                this.handleUserDeath();
              });
              this.dialog.closeAll();
            });
            break;
          }
          case '+Stamina': {
            const value = this.getValueFromEvent(item.event, '+Stamina');
            this.eventService.changeStamina(value).subscribe(() => {
              this.personService.getPersonInfo().subscribe((person: Person) => {
                this.personService.updatePersonStats(person);
              });
              this.dialog.closeAll();
            });
            break;
          }
          case '-HP': {
            const value = this.getValueFromEvent(item.event, '-HP');
            this.eventService.changeHP(-value).subscribe(() => {
              this.personService.getPersonInfo().subscribe((person: Person) => {
                this.personService.updatePersonStats(person);
                this.handleUserDeath();
              });
              this.dialog.closeAll();
            });
            break;
          }
          case '+HP': {
            const value = this.getValueFromEvent(item.event, '+HP');
            this.eventService.changeHP(value).subscribe(() => {
              this.personService.getPersonInfo().subscribe((person: Person) => {
                this.personService.updatePersonStats(person);
              });
              this.dialog.closeAll();
            });
            break;
          }
          case 'food': {
            this.eventService.foundItem('food').subscribe(() => {
              this.eventService.changeStamina(-5).subscribe(() => {
                this.personService.getPersonInfo().subscribe((person: Person) => {
                  this.personService.updatePersonStats(person);
                });
                this.dialog.closeAll();
              });
            });
            break;
          }
          case 'med': {
            this.eventService.foundItem('med').subscribe(() => {
              this.eventService.changeStamina(-5).subscribe(() => {
                this.personService.getPersonInfo().subscribe((person: Person) => {
                  this.personService.updatePersonStats(person);
                });
                this.dialog.closeAll();
              });
            });
            break;
          }
          case '+HP+Stamina': {
            const value = this.getValueFromEvent(item.event, '+HP+Stamina');
            this.eventService.changeStamina(value).subscribe(() => {
              this.eventService.changeHP(value).subscribe(() => {
                this.personService.getPersonInfo().subscribe((person: Person) => {
                  this.personService.updatePersonStats(person);
                });
                this.dialog.closeAll();
              });
            });
            break;
          }
          case '-HP-Stamina': {
            const value = this.getValueFromEvent(item.event, '-HP-Stamina');
            this.eventService.changeStamina(-value).subscribe(() => {
              this.eventService.changeHP(-value).subscribe(() => {
                this.personService.getPersonInfo().subscribe((person: Person) => {
                  this.personService.updatePersonStats(person);
                  this.handleUserDeath();
                });
                this.dialog.closeAll();
              });
            });
            break;
          }
          case 'nothing': {
            this.dialog.closeAll();
            break;
          }
        }
      });
    }
  }

  public generateEvent(): void {
    this.locationService.getEvent().subscribe(item => {
      this.createEventModal(item);
    });
  }

  public activePoints(id: string): void {
    const stepId = document.getElementById(id).id;
    this.stepsLogic(id);
    this.locationService.locationEngine(stepId, this.renderLocations);
    this.locationService.transitionEngine(stepId, this.transitionalArrows);
    this.generateEvent();
  }

  public activeLocation(locationName: string): void {
    const openedDialog = this.dialog.open(ModalComponent, {
      height: '300px',
      width: '300px',
      data: {
        header: locationName,
        locationEvents: [
          {
            action: 'getOut',
            description: ''
          },
          {
            action: 'search',
            description: ''
          },
        ]
      },
      disableClose: true,
    });

    openedDialog.componentInstance.locationEventHandlerAction.subscribe((data: string) => {
      switch (data) {
        case 'getOut': {
          this.dialog.closeAll();
          break;
        }
        case 'search': {
          this.isLoaderShown = true;
          this.locationService.getLocationEvent(locationName).subscribe((data) => {
            if (data === 'exp') {
              this.dialog.open(ModalComponent, {
                height: '220px',
                width: '300px',
                data: {
                  header: 'D.O.O.D.I',
                  text: '   !         " "'
                },
              });
            } else if (data) {
              this.createEventModal(data);
            }
            this.isLoaderShown = false;
          });
          break;
        }
      }
    });
  }

  public activeTransition(name: string): void {
    const transferData = '        ';
    this.dialog.open(ModalComponent, {
      height: '250px',
      width: '400px',
      data: {
        header: `    ${name}`,
        text: transferData,
      },
    });
  }

  private stepsLogic(id: string): void {
    this.steps.forEach((itemFirst: Steps) => {
      if (itemFirst.id === id) {
        itemFirst.isActive = true;
        itemFirst.disabled = false;
        this.currentItemLinks = itemFirst.links;
      } else {
        itemFirst.isActive = false;
        itemFirst.disabled = true;
      }
    });

    this.currentItemLinks.forEach((currentLink: string) => {
      this.steps.forEach((itemSecond: Steps) => {
        if (itemSecond.id === currentLink) {
          itemSecond.disabled = false;
        }
      });
    });
  }

  private transformLocations(data: BackendLocations[]): void {
    this.renderLocations = locations.filter((locationItem: Locations) => {
      return data.some((dataItem: BackendLocations) => {
        if (locationItem.id_slot === dataItem.id_slot) {
          return locationItem.part_name = dataItem.part_name;
        }
      });
    });
  }

}
