import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject } from 'rxjs';

import { environment } from '../../environments/environment';
import { Person } from '../models';

@Injectable({
  providedIn: 'root'
})
export class PersonService {
  public baseApi: string = environment.baseApi;
  public characterId: string = environment.character_id;
  public person = new BehaviorSubject<Person>({
    id: 1,
    character_inventory: [],
    character_name: '',
    character_stamina: 100,
    character_health: 100,
    character_max_health: 100,
    character_max_stamina: 100,
    character_perks: []
  });

  constructor(private http: HttpClient) { }

  public createPerson(): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/create_new_character/`, {
        character_name: 'Name'
      }, {
        headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

  public getPersonInfo(): Observable<Person> {
    return this.http.post<Person>(`${this.baseApi}/get_character_stats/`, {
      character_id: window.localStorage.getItem('character_id')
    }, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public getPersonState(): Observable<any> {
    return this.person;
  }

  public updatePersonStats(person: Person): void {
    this.person.next(person);
  }

  public getPersonInventory(): Observable<Array<string>> {
    return this.http.get<Array<string>>(`${this.baseApi}/get_player_inventory`, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public useItem(item: string): Observable<any> {
    return this.http.get<any>(`${this.baseApi}/use_${item}`, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public getPersonId(): Observable<any> {
    return this.http.get<any>(`${this.baseApi}/get_id/`, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public changeStamina(id: string): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/change_stamina_by_move/`, {
      character_id: window.localStorage.getItem('character_id')
    }, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public healthPerkUp(): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/up_max_health/`, {
      character_id: window.localStorage.getItem('character_id')
    }, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public staminaPerkUp(): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/up_max_stamina/`, {
      character_id: window.localStorage.getItem('character_id')
    }, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public armorPerkUp(): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/up_armor/`, {
      character_id: window.localStorage.getItem('character_id')
    }, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public luckyPerkUp(): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/up_max_slot/`, {
      character_id: window.localStorage.getItem('character_id')
    }, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
    }});
  }

  public useItemFromInventory(index: number): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/character_use_item/`,
      { character_id: window.localStorage.getItem('character_id'), item_id: index },
      {
        headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

}
