import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { environment } from '../../environments/environment';
import { BackendLocations, Locations, TransitionArrows } from '../models';

@Injectable({
  providedIn: 'root'
})
export class LocationService {
  public baseApi: string = environment.baseApi;
  public characterId: string = environment.character_id;

  constructor(private http: HttpClient) { }

  public getLocations(): Observable<BackendLocations[]> {
    return this.http.post<BackendLocations[]>(`${this.baseApi}/get_part_location/`,
      { location_id: 1, character_id: window.localStorage.getItem('character_id') }, {
        headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

  public getEvent(): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/get_event/`, { location_id: 1 }, {
      headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

  public getLocationEvent(locationName: string): Observable<any> {
    return this.http.post<any>(`${this.baseApi}/search_on_place/`,
      { place_name: locationName, character_id: window.localStorage.getItem('character_id') }, {
        headers: { Authorization: 'Token ' + localStorage.getItem('token')
      }}
    );
  }

  public locationEngine(stepId: string, locations: Locations[]): void {
    // tslint:disable-next-line: forin
    for (const key in locations) {
      if (stepId === '005' || stepId === '008') {
        if (locations[key].id_slot === '1') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '1') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '069' || stepId === '066') {
        if (locations[key].id_slot === '2') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '2') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '072' || stepId === '108') {
        if (locations[key].id_slot === '3') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '3') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '118') {
        if (locations[key].id_slot === '4') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '4') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '126' || stepId === '146') {
        if (locations[key].id_slot === '5') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '5') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '172' || stepId === '171' || stepId === '193') {
        if (locations[key].id_slot === '6') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '6') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '211' || stepId === '206') {
        if (locations[key].id_slot === '7') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '7') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '176') {
        if (locations[key].id_slot === '8') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '8') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '238') {
        if (locations[key].id_slot === '9') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '9') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '197' || stepId === '210') {
        if (locations[key].id_slot === '10') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '10') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '167' || stepId === '236') {
        if (locations[key].id_slot === '11') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '11') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '222' || stepId === '216') {
        if (locations[key].id_slot === '12') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '12') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '031' || stepId === '033') {
        if (locations[key].id_slot === '13') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '13') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '047' || stepId === '043') {
        if (locations[key].id_slot === '14') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '14') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '053') {
        if (locations[key].id_slot === '15') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '15') {
          locations[key].disabled = true;
        }
      }

      if (stepId === '226' || stepId === '201') {
        if (locations[key].id_slot === '16') {
          locations[key].disabled = false;
        }
      } else {
        if (locations[key].id_slot === '16') {
          locations[key].disabled = true;
        }
      }
    }
  }

  public transitionEngine(stepId: string, arrows: TransitionArrows[]): void {
    // tslint:disable-next-line: forin
    for (const key in arrows) {
      if (stepId === '012') {
        if (arrows[key].id === 'a') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'a') {
          arrows[key].disabled = true;
        }
      }

      if (stepId === '032') {
        if (arrows[key].id === 'b') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'b') {
          arrows[key].disabled = true;
        }
      }

      if (stepId === '9204') {
        if (arrows[key].id === 'c') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'c') {
          arrows[key].disabled = true;
        }
      }

      if (stepId === '169') {
        if (arrows[key].id === 'd') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'd') {
          arrows[key].disabled = true;
        }
      }

      if (stepId === '244') {
        if (arrows[key].id === 'e') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'e') {
          arrows[key].disabled = true;
        }
      }

      if (stepId === '249') {
        if (arrows[key].id === 'f') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'f') {
          arrows[key].disabled = true;
        }
      }

      if (stepId === '125') {
        if (arrows[key].id === 'g') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'g') {
          arrows[key].disabled = true;
        }
      }

      if (stepId === '117') {
        if (arrows[key].id === 'h') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'h') {
          arrows[key].disabled = true;
        }
      }

      if (stepId === '060') {
        if (arrows[key].id === 'k') {
          arrows[key].disabled = false;
        }
      } else {
        if (arrows[key].id === 'k') {
          arrows[key].disabled = true;
        }
      }

    }
  }

}
